@import '../variables.scss';

.actions {
  @include flex-center;
}

.actions button {
  margin-left: 0.4rem;
  padding: 0.3rem 0.7rem;
}

.actions svg {
  margin-right: 0.45rem;
}

.message {
  margin: 0 0 0.5rem 0;
  text-align: center;
  color: $color-clear-msg;
}

.visible {
  visibility: visible;
}

.hidden {
  visibility: hidden;
}
