@import '../variables.scss';

.sort {
  margin-right: 0.5rem;
  padding: 0;
  background: $color-secondary;
  color: $color-primary-dark;
  height: 100%;
  width: 3rem;
  font-size: 1.3rem;
}

.sort:hover,
.sort:focus {
  background: $color-secondary;
}

.dropdown {
  position: absolute;
  top: 4rem;
  width: 7rem;
  @include flex-column;
  @include bg-2nd;
  @include border;
  @include border-r;
  border-top: none;
  z-index: 100;
  @include shadow;
  @include animation;
}

.dropdown::before {
  content: '';
  position: absolute;
  top: -0.5rem;
  left: 0.85rem;
  height: 1rem;
  width: 1rem;
  @include bg-2nd;
  transform: rotate(45deg);
}

.dropdownHeader {
  margin: 0.3rem 0 0 0;
  padding-left: 0.5rem;
  height: 1.7rem;
  color: $color-dark-grey;
  cursor: default;
  font-weight: 500;
  border-bottom: 1px solid $color-light-gray;
}

.dropdownList {
  list-style: none;
  margin: 0;
  padding: 0;
  @include bg-2nd;
}

.dropdownItem {
  padding-left: 1rem;
  height: 1.7rem;
  width: 100%;
  cursor: pointer;
  @include flex-Ycenter;
  justify-content: space-between;
}

.dropdownItem:hover {
  background: $color-primary;
  color: white;
}

.dropdownItem svg {
  margin: 0 1rem 0.3rem 0;
  color: $color-dark-grey;
}

.dropdownItem:hover svg {
  color: white;
}

.sortUp svg {
  margin: 0.6rem 1rem 0 0;
}

@keyframes slide-down {
  from {
    opacity: 0;
    transform: translateY(-1rem);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
